import {
  Box,
  Button,
  Checkbox,
  Dialog,
  DialogTitle,
  FormControlLabel,
  IconButton,
  MenuItem,
  Paper,
  Select,
  SelectChangeEvent,
  styled,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  useTheme,
} from '@mui/material'
import React, { useCallback, useState } from 'react'
import { ReactComponent as IconClose } from '../../assets/icon/IconClose.svg'
import { ReactComponent as IconConfirm } from '../../assets/icon/IconConfirm.svg'
import { ReactComponent as IconAddCircle } from '../../assets/icon/IconAddCircle.svg'

const LABEL_WIDTH = 125
const LABEL_MR = 12

type FormData = {
  websiteDomain: string
  category: string
  remark: string
}

type Domain = FormData & {
  sourceDomain: string
}

const Label = styled(Typography)({
  width: `${LABEL_WIDTH}px`,
  marginRight: '12px',
  fontSize: '15px',
  fontWeight: 'bold',
  lineHeight: '18px',
  display: 'inline-block',
  textAlign: 'right',
})

const Input = styled(TextField)(({ theme }) => ({
  flex: '1',
  '& .MuiOutlinedInput-root': {
    color: theme.palette.primary.main,
  },
  '& fieldset': {
    borderColor: theme.palette.secondary.main,
  },
}))

const MultiDomainsAddition: React.FC<{ onClose: () => void; onConfirm: () => void }> = ({ onClose, onConfirm }) => {
  const theme = useTheme()
  const [formData, setFormData] = useState<FormData>({ websiteDomain: '', category: 'category 1', remark: '' })
  const [domains, setDomains] = useState<Domain[]>([])

  const onWebsiteDomainChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData((data) => ({ ...data, websiteDomain: e.target.value }))
  }, [])

  const onCategoryChange = useCallback((e: SelectChangeEvent) => {
    setFormData((data) => ({ ...data, category: e.target.value }))
  }, [])

  const onRemarkChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData((data) => ({ ...data, remark: e.target.value }))
  }, [])

  const onDomainAdd = useCallback(() => {
    setDomains((domains) => {
      const newDomains = [...domains, { ...formData, sourceDomain: 'N/A' }]
      return newDomains
    })
    setFormData({ websiteDomain: '', category: 'category 1', remark: '' })
  }, [formData])

  return (
    <Dialog open={true}>
      <Paper sx={{ padding: '26px 35px', width: '543px', position: 'relative' }}>
        <IconButton sx={{ position: 'absolute', top: '18px', right: '26px' }} onClick={onClose}>
          <IconClose />
        </IconButton>
        <DialogTitle
          color='primary'
          sx={{ fontSize: '20px', fontWeight: 'bold', lineHeight: '24px', paddingLeft: '0px' }}
        >
          Add domain to whitelist
        </DialogTitle>

        <Box sx={{ marginBottom: '28px', border: '1px solid #2E89FF80' }} />

        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Label color='primary'>Website Domain : </Label>
          <Input size='small' value={formData.websiteDomain} onChange={onWebsiteDomainChange} />
        </Box>

        <Box sx={{ marginTop: '24px', display: 'flex', alignItems: 'center' }}>
          <Label color='primary'>Category : </Label>
          <Select
            value={formData.category}
            onChange={onCategoryChange}
            size='small'
            sx={{
              flexGrow: 1,
              color: theme.palette.primary.main,
              '& fieldset': {
                borderColor: theme.palette.secondary.main,
              },
            }}
          >
            <MenuItem value='category 1' sx={{ color: theme.palette.primary.main }}>
              category 1
            </MenuItem>
            <MenuItem value='category 2' sx={{ color: theme.palette.primary.main }}>
              category 2
            </MenuItem>
          </Select>
        </Box>

        <Box sx={{ marginTop: '24px', display: 'flex', alignItems: 'start' }}>
          <Label color='primary'>Remark : </Label>
          <Input
            size='small'
            color='primary'
            multiline
            rows={4}
            placeholder='100 characters (optional)'
            value={formData.remark}
            onChange={onRemarkChange}
          />
        </Box>

        <Box
          sx={{
            marginTop: '19px',
            marginLeft: `${LABEL_WIDTH + LABEL_MR}px`,
            display: 'flex',
            flexGrow: 1,
            justifyContent: 'space-between',
          }}
        >
          <FormControlLabel
            label={<Typography sx={{ fontSize: '15px' }}>Include all source domain</Typography>}
            sx={{ color: theme.palette.primary.main, fontSize: '15px' }}
            control={<Checkbox defaultChecked color='secondary' sx={{ color: theme.palette.secondary.main }} />}
          />

          <Button
            disabled={!formData.websiteDomain}
            color='secondary'
            variant='contained'
            startIcon={<IconAddCircle fill={formData.websiteDomain ? '#FFFFFF' : '#00000042'} />}
            sx={{ height: '40px' }}
            onClick={onDomainAdd}
          >
            Add
          </Button>
        </Box>

        <Box mt='23px' />
        <TableContainer
          sx={{
            border: `1px solid ${theme.palette.secondary.main}`,
            height: '140px',
            '& .header': {
              background: theme.palette.secondary.main,
              color: '#FFFFFF',
              padding: '6px 12px',
            },
            '& .domain': { padding: '3px 12px', border: 'none' },
          }}
        >
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell className='header'>Website Domain</TableCell>
                <TableCell className='header'>Source domain</TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              {domains.map((domain, idx) => (
                <TableRow key={idx}>
                  <TableCell className='domain'>{domain.websiteDomain}</TableCell>
                  <TableCell className='domain'>{domain.sourceDomain}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

        <Box sx={{ marginTop: '15px', textAlign: 'right' }}>
          <Button
            disabled={domains.length === 0}
            color='secondary'
            variant='contained'
            startIcon={<IconConfirm fill={domains.length === 0 ? '#00000042' : '#FFFFFF'} />}
            onClick={onConfirm}
          >
            Confirm
          </Button>
        </Box>
      </Paper>
    </Dialog>
  )
}
export default MultiDomainsAddition
