import {
  Button,
  styled,
  Table,
  TableBody,
  TableCell as _TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TableSortLabel,
  useTheme,
} from '@mui/material'
import { DOMAINS } from './data'
import { ReactComponent as IconRemove } from '../../assets/icon/IconRemove.svg'
import { useState } from 'react'
import { Order } from '../types'
import { Domain } from './types'
import MsgDialog from '../../shared/component/MsgDialog'

const headers = [
  {
    id: 'name',
    label: 'Domain',
  },
  {
    id: 'websiteTitle',
    label: 'Website Title',
  },
  {
    id: 'category',
    label: 'Category',
  },
  {
    id: 'remark',
    label: 'Remark',
  },
  {
    id: 'creationDate',
    label: 'Creation Date',
  },
]

const TableCell = styled(_TableCell)({
  padding: '9px 0px',
})

const DomainList: React.FC<{}> = () => {
  const theme = useTheme()
  const [order, setOrder] = useState<Order>('asc')
  const [orderBy, setOrderBy] = useState<keyof Domain>('name')
  const [deletedDomain, setDeletedDomain] = useState<Domain | null>(null)

  return (
    <>
      <TableContainer sx={{ maxHeight: '600px' }}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell />
              {headers.map((header) => (
                <TableCell
                  key={header.id}
                  sortDirection={orderBy === header.id ? order : 'asc'}
                  onClick={() => {
                    if (orderBy === header.id) {
                      setOrder(order === 'asc' ? 'desc' : 'asc')
                      return
                    }

                    setOrder('asc')
                    setOrderBy(header.id as keyof Domain)
                  }}
                >
                  <TableSortLabel active={orderBy === header.id} direction={orderBy === header.id ? order : 'asc'}>
                    {header.label}
                  </TableSortLabel>
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>

          <TableBody>
            {DOMAINS.map((domain, idx) => (
              <TableRow key={idx}>
                <TableCell>{idx + 1}</TableCell>
                <TableCell sx={{ color: theme.palette.secondary.main }}>{domain.name}</TableCell>
                <TableCell>{domain.websiteTitle}</TableCell>
                <TableCell>{domain.category}</TableCell>
                <TableCell>{domain.remark}</TableCell>
                <TableCell>{domain.creationDate}</TableCell>
                <TableCell>
                  <Button
                    variant='contained'
                    color='secondary'
                    startIcon={<IconRemove />}
                    sx={{ py: '4px', fontSize: '15px' }}
                    onClick={() => setDeletedDomain(domain)}
                  >
                    Delete
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {deletedDomain && (
        <MsgDialog
          title='Delete'
          msg={`Confirm delete ${deletedDomain.name} in this whitelist`}
          buttonLabel='Confirm'
          onClose={() => setDeletedDomain(null)}
        />
      )}
    </>
  )
}

export default DomainList
