import React, { useState } from 'react'
import Header from '../../shared/header'
import Navigator from '../../shared/navigator'
import Search from '../../shared/component/Search'
import { Link, useParams } from 'react-router-dom'
import { Box, Breadcrumbs, Button, Paper, styled, Typography, useTheme } from '@mui/material'
import { Main } from '../../shared/main'
import { WHITELISTS } from './data'
import { ReactComponent as IconAddCircle } from '../../assets/icon/IconAddCircle.svg'
import { ReactComponent as IconFilter } from '../../assets/icon/IconFilter.svg'
import { ROUTES } from '../../constants'
import DomainList from './DomainList'
import MultiDomainsAddition from './MultiDomainsAddition'

const BoldText = styled(Typography)(({ theme }) => ({
  fontWeight: 'bold',
  color: theme.palette.primary.main,
}))

const WhitelistDetail: React.FC<{}> = () => {
  const theme = useTheme()
  const { whitelistId } = useParams()
  const [showDialog, toggleDialog] = useState(false)
  const whitelist = WHITELISTS[parseInt(whitelistId as string) - 1]

  return (
    <>
      <Header />
      <Navigator page='whitelist' />
      <Main>
        <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Breadcrumbs color='primary' separator='>' sx={{ fontSize: '20px', lineHeight: '24px', fontWeight: 'bold' }}>
            <Link to={ROUTES.whitelist} style={{ color: theme.palette.primary.main, textDecoration: 'none' }}>
              Whitelist
            </Link>
            <Typography color='secondary' sx={{ fontWeight: 'bold', fontSize: '20px' }}>
              {whitelist.name}
            </Typography>
          </Breadcrumbs>

          <Box display='flex'>
            <Button
              color='secondary'
              startIcon={<IconAddCircle fill={theme.palette.secondary.main} />}
              sx={{
                background: '#FFFFFF',
                boxShadow: '0px 3px 6px #00000029',
                p: '10px 16px',
                fontSize: '15px',
                fontWeight: 'normal',
                lineHeight: '20px',
              }}
              onClick={() => toggleDialog(true)}
            >
              Add domain
            </Button>

            <Box ml='16px' />
            <Search />
          </Box>
        </Box>

        <Box mt='28px' />
        <Paper sx={{ padding: '24px 46px', boxShadow: '0px 3px 6px #00000029' }}>
          <Box display='inline-flex'>
            <BoldText>Group: </BoldText>
            <Box ml='8px' />
            <Typography color='secondary'>{whitelist.groups.map((group) => group.name).join(', ')}</Typography>
          </Box>
          <Box sx={{ display: 'inline-flex', marginLeft: '200px' }}>
            <BoldText>IP:</BoldText>

            <Box ml='8px' />
            <Typography color='secondary'>{whitelist.ip}</Typography>
          </Box>
        </Paper>

        <Box mt='32px' />
        <Paper sx={{ padding: '26px 28px', boxShadow: '0px 3px 6px #00000029' }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography
              sx={{
                background: theme.palette.secondary.main,
                color: '#FFFFFF',
                padding: '4px 12px',
              }}
            >
              {`Website domain: ${whitelist.domainCount}`}
            </Typography>
            <Button
              color='secondary'
              startIcon={<IconFilter />}
              sx={{ boxShadow: '0px 3px 6px #00000029', padding: '11px 26px' }}
            >
              Filter
            </Button>
          </Box>

          <Box mt='38px' />
          <DomainList />
        </Paper>
      </Main>
      {showDialog && <MultiDomainsAddition onClose={() => toggleDialog(false)} onConfirm={() => toggleDialog(false)} />}
    </>
  )
}

export default WhitelistDetail
